import React, { ReactElement, useCallback, useState, useEffect, useMemo, createRef, FormEvent } from 'react';
import { GET, server, POST } from '../utils/requests';
import { Player, TournamentSettings, ID, History, PairingObject } from '../interfaces';
import url from 'url';

import styles from '../css/profile.module.css';
import sidebarStyles from '../css/sidebar.module.css';
import { IoMdClose } from 'react-icons/io';
import { Bottom } from '../components/Buttons';
import CollapsableSection, { SectionBreak } from '../components/CollapsableSection';
import { AiOutlineCheck, AiOutlineClose } from 'react-icons/ai';
import { AxiosRequestConfig } from 'axios';

interface ActiveProps {
	player: Player
    clearClash: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
    setDisplayPlayer: (id: string) => void
	requireLastName: boolean
}

export function ActivePlayer(props: ActiveProps): ReactElement {

	const displayName = props.requireLastName ?
		[props.player.firstName, props.player.lastName].join(' ') :
		props.player.firstName;
	
	return (
		<div id={props.player.id} className={[sidebarStyles.playerLine, styles.playerLine].join(' ')}>
			<div
				className={['playerInfo', sidebarStyles.playerInfo].join(' ')}
				onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).parentElement.id)}
			>
				{displayName}
			</div>
			{
                <button name='cancel' className={[sidebarStyles.cancel, styles.cancel].join(' ')} onClick={(props.clearClash)}>
                    <AiOutlineClose />
                </button>
            }
		</div>
	);

}

interface FormProps {
	id: string
	onSubmit: (e: React.FormEvent<HTMLFormElement>) => Promise<void>
    action: string
    player: Player
    players: Player[]
    getDisplayName: (player: string) => string
}

export function SingleClash(props: FormProps) {

    const firstInput = createRef() as React.RefObject<HTMLInputElement>;
    const [focus, mustFocus] = useState(false);

    useEffect(() => {
        if (!focus) return;
        if (!firstInput.current) return;
        firstInput.current.focus();
    }, [firstInput, focus]);

    const handleInput = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
        let input = e.target as HTMLInputElement;
        let text = input.value;
        if (!text.trim()) return;
        let length = text.length;
        let choices = props.players.filter(p => p.id !== props.player.id && props.getDisplayName(p.id).toLowerCase().startsWith(text.toLowerCase()));
        let firstChoice = choices[0];
        if (!firstChoice) return;
        let autofill = props.getDisplayName(firstChoice.id);
        input.value = autofill;
        input.select();
        input.setSelectionRange(length, autofill.length, 'forward');
    }, [props.players])
	
	return (
		<div className={styles.scoreSection}>
			<form
				target='_self'
				onSubmit={(e) => props.onSubmit(e).then(() => mustFocus(true))}
				method='POST'
				action={props.action}
			>
                <input type='text' className={[sidebarStyles.input, styles.input].join(' ')} name='oppName' required
                    placeholder='Opponent name'
                    onInput={handleInput}
                    autoComplete={'off'}
				/>
				<button tabIndex={-1} className={sidebarStyles.submit}>
					<AiOutlineCheck />
				</button>
			</form>
		</div>
	);
}

interface ProfileProps {
	id: string
	playerID: string
	setDisplayPlayer: (id: string) => void
	playerDict: Map<ID, Player>
	modifyPlayerDict: (p: Player, hash: string) => void
	settings: TournamentSettings
	playerDictKey?: string
}

export default function Profile(props: ProfileProps): ReactElement {
    
    const [playerID, setPlayerID] = useState('');
    const [player, setPlayer] = useState(null as Player);

    const requireLastName = useMemo(() => props.settings.competitors === 'individual', [props.settings.competitors]);

	const updatePlayerFromProps = (refetch?: boolean) => {
		setPlayerID(playerID);		
		if (!props.playerID) {
			setPlayer(null as Player);
			return;
		}
		if (refetch === false) {
			setPlayer(props.playerDict.get(props.playerID));
		} else {
			return GET({
				url: '/tournament/' + props.id + '/fetchPlayer',
				params: {
					id: props.playerID
				}
			})
				.then((_player: Player) => {
					if (!_player.histories[0]) _player.histories[0] = null;
					setPlayer(_player);
				});
		}
	}

	const getNameFromPlayer = useCallback((_player: Player): string  =>{
		if (!_player) return '\u200b';
		if (requireLastName) return [_player.firstName, _player.lastName].join(' ');
		else return _player.firstName;
	}, [requireLastName]);
    
    useEffect(() => {
        updatePlayerFromProps();
    }, [props.playerID, props.playerDictKey])

	const getDisplayName = useCallback((id: ID): string => {
        if (!id) return '\u200b';
		if (id === 'bye') return 'BYE';
		let opp = props.playerDict.get(id);
		if (!opp) return id;
		let displayName: string;
		if (requireLastName) displayName = [opp.firstName, opp.lastName].join(' ');
		else displayName = opp.firstName;
		return displayName;
    }, [props.playerDict, requireLastName]);
    
    const renderEntry = useCallback(([k, v]: [string, any], i: number): ReactElement => {
        if (k === 'Playing' && v) {
            return (
                <div className={styles.row} key={['row', i].join('.')}>
                    <div className='fieldBox'>{k + ':'}</div>
                    <div
                        className='playerInfo'
                        id={v as string}
                        onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).id)}
                    >{getDisplayName(v)}</div>
                </div>
            );
        } else {
            return (
                <div className={styles.row} key={['row', i].join('.')}>
                    <div className='fieldBox'>{k + ':'}</div>
                    <div className='fieldBox'>{v !== null && v!== '' ? v : '\u200b'}</div>
                </div>
            );
        }
    }, [props.setDisplayPlayer, getDisplayName]);    

    const [clashes, setClashes] = useState([] as PairingObject[]);

    const updateClashes = useCallback(async () => {
        if (!player || !player.id) return;
        GET({
            url: '/tournament/' + props.id + '/fetchClashes',
            params: { id: player.id }
        })
            .then(setClashes)
    }, [props.id, setClashes, player]);
    const clearClash = useCallback(async (oppId) => POST({
        url: '/tournament/' + props.id + '/clearClash',
        data: {
            playerId: player.id,
            oppId
        }
    })
        .then(updateClashes), [props.id, player, updateClashes]);

    const List = useMemo((): ReactElement[] => {
        if (!props.playerDict.size) return null;
        let list = [] as ReactElement[];
        for (let i = 0; i < clashes.length; i++) {
            let opp = props.playerDict.get(clashes[i][1]);
            if (!opp) continue;
            list.push(<ActivePlayer
                key={['clash', i].join('.')}
                player={opp}
                requireLastName={requireLastName}
                clearClash={() => clearClash(opp.id)}
                setDisplayPlayer={props.setDisplayPlayer}
            />);
        }
        return list;
    }, [props.playerDict, props.playerDictKey, clashes, clearClash, props.setDisplayPlayer, requireLastName]);

    useEffect(() => {
        updateClashes();
    }, [updateClashes]);

    const players = useMemo(() => Array.from(props.playerDict.values()), [props.playerDict, props.playerDictKey]);

    if (!player) return null;
    let Name = getNameFromPlayer(player);
    const profile = !player ? {} : {
        'Unique ID': player.id,
        Status: player.active !== false ? 'Active' : 'Inactive',
        Rating: player.rating,
        'Performance rating': player.performanceRating,
        Playing: player.isPlaying
    };
    let history = player.histories;

    const total = (Math.max(player.histories.length, 1) - 1 - (player.isPlaying ? 1 : 0)).toString();
    const totalGames = player.histories.reduce((acc, curr) => {
        if (!curr) return acc;
        acc += curr.played
        return acc;
    }, 0);
    const scores = !player ? {} : {
        Score: player.score.toString() + ' / ' + total,
        'Game score': player.gamePoints.toString() + ' / ' + totalGames.toString(),
        'Expected score': player.expectedScore.toFixed(2),
        'Expected game score': player.expectedGameScore.toFixed(2) + ' / ' + totalGames,
    };
    if (!props.settings.displayPoints) {
        delete scores['Game score'];
        delete scores['Expected game score'];
    }

    return (
        <>
            <div className={styles.profile}>		
                <div className='topBanner'>
                    {Name}
                </div>
				<div className={[styles.profileContainer, 'scrollable'].join(' ')}>
					{Object.entries(profile).map(renderEntry)}
					<CollapsableSection
						header='Score'
						useInternalState={true}
						style={{ gridColumn: '1 / span 2' }}
					>
						{Object.entries(scores).map(renderEntry)}
					</CollapsableSection>
					{props.settings.allowClashes ? <CollapsableSection
						header='Forbidden Pairings'
						useInternalState={true}
						style={{ gridColumn: '1 / span 2' }}
					>
						{List}
						<SingleClash
							id={props.id}
							getDisplayName={getDisplayName}
							onSubmit={(e: FormEvent<HTMLFormElement>): Promise<void> => {
								e.preventDefault();
								e.persist();
								let form = e.target as HTMLFormElement & { oppName: HTMLInputElement };
								let opp = players.find(p => getDisplayName(p.id) === form.oppName.value);
								let options = {
									data: {
										playerId: player.id,
										oppId: opp.id
									},
									url: form.action
								} as AxiosRequestConfig;
								return POST(options)
									.then(() => {
										let form = e.target as HTMLFormElement;
										form.reset();
									})
									.then(updateClashes)
							}}
							player={player}
							players={players}
							action={url.resolve(server, ['tournament', props.id, 'createClash'].join('/'))}
						/>
					</CollapsableSection> : null}
					<CollapsableSection
						header='Results'
						useInternalState={true}
						style={{ gridColumn: '1 / span 2' }}
					>
						{history.map((minPlayer: History, i: number): ReactElement => {
							if (i === 0 || !minPlayer) return null;
							let { id, colour } = minPlayer;
							let result = minPlayer[props.settings.displayPoints];
							return (
								<div className={styles.resultsRow} key={['row', 'history', i].join('.')}>
									<div className='fieldBox'>{i}</div>
									<div
										className='playerInfo'
										id={id}
										onClick={(e: React.MouseEvent<HTMLDivElement>) => props.setDisplayPlayer((e.target as HTMLDivElement).id)}
									>{getDisplayName(id)}</div>
									<div className='fieldBox'>{colour || '\u200b'}</div>
									<div className='fieldBox'>{typeof result !== 'undefined' && result !== null ? result : '\u200b'}</div>
								</div>
							);
						})}
					</CollapsableSection>
					<SectionBreak />
				</div>
            </div>
            <div className={['bottomBanner', styles.bottomBanner].join(' ')}>
                {Bottom([() => props.setDisplayPlayer(''), <IoMdClose />, 'Close'])}
            </div>	
        </>
    );
}